import { Component, OnInit } from '@angular/core';
import { API } from "../../../custom-components/services/api";

@Component({
  selector: 'area-demo-basic',
  template: `
    <p>level属性可选值为0,1,2；分别代表省市县级别</p>
    <div style="width: 200px;margin: 20px 0;">
      <area-select [(ngModel)]="areaCode" [label]="defaultLabel"></area-select>
    </div>
    <p style="margin: 40px 0 20px 0">只选省级别level="0"</p>
    <div style="width: 200px;">
      <area-select [(ngModel)]="areaCode" [label]="defaultLabel" level="0"></area-select>
    </div>
    <div style="margin: 20px 0;">{{areaCode}}</div>
    <div>   <button (click)="doTest()">测试取区域Code</button></div>
  `,
  styles: []
})
export class AreaDemoBasicComponent implements OnInit {
  areaCode:String = "110106000000";
  defaultLabel:string='请选择区域…';
  ngOnInit() {
  }
  doTest():any{
    alert(this.areaCode);
  }


}



