import { Component, OnInit } from '@angular/core';
import { API } from "../../../custom-components/services/api";

@Component({
  selector: 'area-demo-select',
  template: `    
    <div>
      <city-dropdown  [(province)]="provinceCode" [(city)]="cityCode" [(district)]="districtCode"></city-dropdown>
    </div>
    <div style="margin-top: 20px;">
      <span>省：{{provinceCode}}</span>
      <span>市：{{cityCode}}</span>
      <span>县：{{districtCode}}</span>
    </div>
  `,
  styles: []
})
export class AreaDemoSelectComponent implements OnInit {
  districtCode:String = "";
  cityCode:String = "";
  provinceCode:String = "";
  ngOnInit() {
  }



}



