import { NgModule } from '@angular/core';
import { RouterModule } from "@angular/router";
import { AreaDemoComponent } from "./area-demo.component";



@NgModule({
	imports: [ RouterModule.forChild([
	  { path: '', component: AreaDemoComponent }
	]) ],
	exports: [ RouterModule ]
  })
  export class AreaDemoRoutingModule {
  }



